import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import { getListRankTraining, deleteListRankTraining } from 'services/RangkTraining';
import { Card, Row, Col, message } from 'antd';

class RankLearning extends Component {
  state = {
    reload: 0,
  };

  handleDelete = async id => {
    await deleteListRankTraining(id);
    message.success('Xóa thành công');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const columns = [
      {
        title: 'Xếp hạng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Từ tín chỉ',
        dataIndex: 'fromCredit',
        key: 'fromCredit',
      },
      {
        title: 'Đến tín chỉ',
        dataIndex: 'toCredit',
        key: 'toCredit',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            hrefEdit={`#rank-training/form/edit/${record.id}`}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    const { reload } = this.state;
    return (
      <Card title="Danh mục xếp hạng năm đào tạo">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/rank-training/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getListRankTraining}
              columns={columns}
              rowKey={record => record.label + record.id}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RankLearning />
  </Page>
);



